.App {
  text-align: center;
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
}
#wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.window {
  border-top: solid 1px black;
  border-bottom: solid 1px black;
  border-radius: 2%;
  margin: 3vh 0px;
  overflow: hidden;
}
#editor-window {
  font-size: 2vmin;
  width: 50vw;
  height: 45vh;
}
#preview-window {
  width: 70vw;
  color: black;
  background-color: rgb(120, 197, 197);
  font-size: calc(5px + 2vmin);
}
.header {
  color: white;
  background-color:darkslategrey;
  border-bottom: solid 2px black;
  
}
#editor {
  width: 100%;
  height: 100%;
  overflow-y: scroll;
  font-size: inherit;
  background-color: rgb(130, 177, 177);
}
#preview {
  width: 100%;
  height: 100%;
  font-size: inherit;
  text-align: left;
  padding: 20px;

}
blockquote {
  border-left: 2px solid black;
  padding-left: 5px;
}
code {
  background: white;
  padding: 1px 4px 2px 4px;
  font-weight: bold;
}
pre {
  background: white;
  padding: 5px;
  margin-right: 50px;
  margin-left: 15px;
}
h1 {
  border-bottom: 3px solid black;
  margin-right: 50px;
}
h2 {
  border-bottom: 2px solid black;
  margin-right: 50px;
}
h3 {
  border-bottom: 1px solid black;
  margin-right: 50px;
}
h4 {
  border-bottom: 0.5px solid black;
  margin-right: 50px;
}

::-webkit-scrollbar {
width: 15px;
height: 15px;
}

::-webkit-scrollbar-track {
background: #f5f5f5;
border-radius: 0px;
}

::-webkit-scrollbar-thumb {
border-radius: 0px;
background: #ccc;
}

::-webkit-scrollbar-thumb:hover {
background: #999;  
}
